.known-for-credits-card{margin-right:10px;width:150px;cursor:pointer;box-shadow:10px 10px 5px -6px #aae3b1}.known-for-credits-card img{border-radius:5px}.known-for-credits-card .known-for-credits-image{font-size:3rem;background-color:#b4b2b2;width:150px;height:225px;text-align:center;padding-top:90px;overflow:hidden}.known-for-credits-card .known-for-credit-title{text-align:center;margin-top:0.5rem;font-size:0.9rem}

.People-detail-page-filmography{display:flex;flex-direction:column;align-items:center}

.Video-Title{margin:2.5rem 0;font-weight:bold;font-size:1.1rem}.People-Detail-Filmography-Table{width:80%;border:1px solid lightgray;border-collapse:collapse;box-shadow:16px 22px 17px -17px #136b0a}.People-Detail-Filmography-Table caption{padding-top:5rem}.People-Detail-Filmography-Table thead{background-color:#234723}.People-Detail-Filmography-Table th,.People-Detail-Filmography-Table td{padding:10px 20px}.People-Detail-Filmography-Table th{color:white}.People-Detail-Filmography-Table td{border-bottom:1px solid #e6c2c2}.People-Detail-Filmography-Table td .title{font-size:1.1rem;font-weight:bold;cursor:pointer;-webkit-transition:color 0.1s ease-in-out;transition:color 0.1s ease-in-out;color:#3f614e}.People-Detail-Filmography-Table td .title:hover{color:lightslategrey}.People-Detail-Filmography-Table td .as{color:lightslategrey;font-size:0.9rem;padding:0 0.5rem}

.People-Cast-For-Show{display:grid;grid-template-columns:repeat(auto-fit, minmax(468px, 1fr));padding:2rem;width:85vw}.People-Cast-For-Show .People-cast-container{margin:1rem}.People-Cast-For-Show .People-cast-container .full-cast-role-title{font-size:1.5rem;padding-left:2rem;font-weight:bold}.People-Cast-For-Show .People-cast-container .full-cast-role-title .full-cast-number{color:#727171}.People-Cast-For-Show .People-cast-container .credit-container{color:green;margin:1rem;padding:0.5rem}.People-Cast-For-Show .People-cast-container .credit-container .grid-items{background-color:#d5f3e9;padding:0.4rem 0rem 1.3rem 0;border-radius:10px;display:flex;flex-direction:column;justify-content:center;align-items:center}.People-Cast-For-Show .People-cast-container .credit-container .grid-items .individual-item{background-color:#fff;width:95%;margin-top:1rem;padding:1rem 0;border:1px solid #b6ecb6;border-radius:5px;display:flex}.People-Cast-For-Show .People-cast-container .credit-container .grid-items .individual-item .image-container{padding-left:1rem}.People-Cast-For-Show .People-cast-container .credit-container .grid-items .individual-item .image-container .no-image{background-color:#adadad;width:66px;height:66px;font-size:3rem;text-align:center;color:#5c5b5e}.People-Cast-For-Show .People-cast-container .credit-container .grid-items .individual-item .item-content{padding-left:1rem}.People-Cast-For-Show .People-cast-container .credit-container .grid-items .individual-item .item-content .item-content-name{font-weight:bold;color:#6b6b6b;margin-bottom:0}.People-Cast-For-Show .People-cast-container .credit-container .grid-items .individual-item .item-content .item-content-character{color:#2b2a2a}.People-Cast-For-Show .People-cast-container .credit-container .no-content{color:#646464;padding-left:2rem;font-weight:bold;padding:1rem 2rem !important}

.Floating{position:fixed;bottom:5.4vh;right:5vw;z-index:9999}.Floating li,.Floating a{background:linear-gradient(45deg, #db3737 40%, #e77575 90%);z-index:1;position:relative}.Floating a::before{position:absolute;content:"";top:0;right:0;bottom:0;left:0;border-radius:28px;background:linear-gradient(45deg, #e77575 40%, #6bfe7f 90%);z-index:-1;-webkit-transition:opacity 0.5s linear;transition:opacity 0.5s linear;opacity:0}.Floating a:hover::before{opacity:1}

.Footer{background-color:#031f37;color:white;width:100%;padding:5rem 1rem}.footerTitle{font-size:1.8rem;color:beige;padding-bottom:1rem}.cards{margin:0 2rem;display:grid;grid-template-columns:1fr 2fr 1fr;grid-gap:10px}.card{padding:2rem;display:flex;flex-direction:column;align-items:center;background-color:white;padding-bottom:2rem;border-radius:5px}.cardTitle{font-size:1.4rem;font-weight:bold;margin:1rem 0;color:#5e5d5d;border-bottom:1px solid gray}.cardChips{display:flex;flex-wrap:wrap;justify-content:center;align-items:center}.cardBody{color:darkgray;padding-top:10vh;display:flex;flex-direction:column;align-items:flex-end}.skills{display:flex}.copyright{margin-top:3rem;display:flex;justify-content:center;align-items:center;letter-spacing:3px;font-size:0.8rem}.link{cursor:pointer;height:1.2rem;width:100%;margin:0.5rem;-webkit-transition:all 0.5s ease;transition:all 0.5s ease;color:green}.link:hover{color:#052b18;font-size:1.1rem}@media (max-width: 800px){.cards{grid-template-columns:1fr}}

